<!--  -->
<template>
  <div class='navbox'>
    <div class="nav" :class="{clishaa:clish==1}" @click="home">首页</div>
    <div class="nav" :class="{clishaa:clish==2}" @click="elses">其他</div>
  </div>
</template>

<script>
  export default {
    name: '',
    data() {
      //这里存放数据
      return {
        clish: 1
      };
    },
    //生命周期 - 挂载完成（可以访问DOM元素）
    mounted() {
      console.log(this.clish)

    },
    //方法集合
    methods: {
      home() {
        var query = window.location.href;
        let urls = query.split("#")
        if (urls[1] != "/") {
          this.$router.push('/')

        }
        this.clish = 1
        console.log(this.clish)
      },
      elses() {
        var query = window.location.href;
        let urls = query.split("#")
        if (urls[1] != "/elses") {
          this.$router.push('/elses')

        }
        this.clish = 2
        console.log(this.clish)

      }
    },
    //生命周期 - 创建完成（可以访问当前this实例）
    created() {

    },
    //监听属性 类似于data概念
    computed: {},
    //监控data中的数据变化
    watch: {},
    //beforeCreate() {}, //生命周期 - 创建之前
    //beforeMount() {}, //生命周期 - 挂载之前
    //beforeUpdate() {}, //生命周期 - 更新之前
    //updated() {}, //生命周期 - 更新之后
    //beforeDestroy() {}, //生命周期 - 销毁之前
    //destroyed() {}, //生命周期 - 销毁完成
    //activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
  }

</script>
<style lang='' scoped>
  /* @import url(); 引入公共css类 */
  .navbox {
    width: 100%;
    height: 50px;
    position: fixed;
    bottom: 0;
    left: 0;
    background: #ffffff;
    border-top: 1px solid #eeeeee;
  }

  .nav {
    width: 50%;
    text-align: center;
    line-height: 50px;
    float: left;
  }

  .clishaa {
    background: red;
  }

</style>



